import React, {useState, useEffect} from 'react'
import {Link, useNavigate} from "react-router-dom"
import {Row, Col, Image, ListGroup, Button, Card, Form} from "react-bootstrap"
import Rating from '../components/Rating'
import {useSelector, useDispatch} from 'react-redux'
import {useParams} from "react-router-dom"
import {listProductDetails, createProductReview} from '../actions/productActions'
import Loader from '../components/Loader'
import Message from '../components/Message'
import { PRODUCT_REVIEW_CREATE_RESET } from '../constants/productConstants'


function ProductScreen() {
    const navigate = useNavigate()
    const {id} = useParams();
    const dispatch = useDispatch()

    const [qty, setQty] = useState(1)
    const [rating, setRating] = useState(0)
    const [comment, setComment] = useState('')


    const productDetails = useSelector(state => state.productDetails)
    const {error, loading, product} = productDetails

    const userLogin = useSelector(state => state.userLogin)
    const {userInfo} = userLogin

    const productReviewCreate = useSelector(state => state.productReviewCreate)
    const {error: errorProductReview, loading: loadingProductReview, success: successProductReview} = productReviewCreate


    // const product = products.find((p)=>p._id == id)

    // const [product, setProduct] = useState([])

    useEffect(()=>{
    //   async function fetchProducts(){
    //     const {data} = await axios.get(`/api/products/${id}`)
    //     setProduct(data)
    //     console.log(data)
    //   }
  
    //   fetchProducts()
        if(successProductReview){
            setRating(0)
            setComment('')
            dispatch({type: PRODUCT_REVIEW_CREATE_RESET})
        }

        dispatch(listProductDetails(id))
    
    }, [dispatch, id, successProductReview])

    const addToCartHandler = () => {
        navigate(`/cart/${id}/?qty=${qty}`)
    }

    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(createProductReview(id, {
            rating, 
            comment
        }))
    }

  return (
    <div>
        <Link to="/" className='btn btn-light my-3'>Go Back</Link>
        {
            loading ? <Loader/> 
            : error ? <Message variant='danger'>{error}</Message>
            : (
            <div>
                <Row>
                <Col md={6}>
                    <Image src={product.image} alt={product.name} fluid/>
                </Col>
    
                <Col md={3} >
                    <ListGroup variant='flush' >
    
                        <ListGroup.Item>
                            <h3>{product.name}</h3>
                        </ListGroup.Item>
    
                        <ListGroup.Item>
                            <Rating text={`${product.numReviews}`} value={product.rating} color={"#f8e825"}/>
                        </ListGroup.Item>
    
                        <ListGroup.Item>
                            Price: ${product.price}
                        </ListGroup.Item>
    
                        <ListGroup.Item>
                            Description: ${product.description}
                        </ListGroup.Item>
    
    
                    </ListGroup>
                </Col>
    
                <Col md={3} >
                    <Card>
                        <ListGroup variant="flush" >
                            <ListGroup.Item>
                                <Row>
                                    <Col>Price:</Col>
                                    <Col>
                                        <strong>{product.price}</strong>
                                    </Col>
                                </Row>
                            </ListGroup.Item>
    
                            <ListGroup.Item>
                                <Row>
                                    <Col>Status:</Col>
                                    <Col>
                                        {product.countInStock > 0 ? 'In Stock' : 'Out of Stock'}
                                    </Col>
                                </Row>
                            </ListGroup.Item>

                            {product.countInStock > 0 && (
                                <ListGroup.Item>
                                    <Row>
                                        <Col>Qty</Col>
                                        <Col className='my-1' xs='auto'>
                                            <Form.Control 
                                                as='select'
                                                value={qty} 
                                                onChange={e=>setQty(e.target.value)}>
                                                {[...Array(product.countInStock).keys()].map((x)=>(
                                                    <option key={x+1} value={x+1} >
                                                        {x+1}
                                                    </option>
                                                ))}
                                            </Form.Control>
                                        </Col>
                                    </Row>
                                </ListGroup.Item>
                            )}
    
                            <ListGroup.Item>
                                <Button className='btn-block' type='button' disabled={product.countInStock===0} 
                                    onClick={addToCartHandler}
                                >Add to Cart </Button>
                            </ListGroup.Item>
                        </ListGroup>
                    </Card>
                </Col>
            </Row>
            
            <Row className='my-4'>
                <Col md={6} >
                    <h4>Reviews</h4>
                    {product.reviews.length === 0 && <Message variant='info' >No Reviews</Message>}

                    <ListGroup variant='flush'>
                        {product.reviews.map((review) => (
                            <ListGroup.Item key={review._id} >
                                <strong>{review.name}</strong>
                                <Rating value={review.rating} color='#f8e825' />
                                <p>{review.createdAt.substring(0,10)}</p>
                                <p>{review.comment}</p>
                            </ListGroup.Item>
                        ))}

                        <ListGroup.Item>
                            <h4>Write a Review</h4>
                            {loadingProductReview && <Loader/>}
                            {successProductReview && <Message variant={'info'} >Review has been submitted successfully</Message>}
                            {errorProductReview && <Message variant={'danger'} >{errorProductReview}</Message>}
                            {userInfo ? (
    
                            <Form onSubmit={submitHandler}>
                               <Form.Group controlId='rating'>
                                   <Form.Label>Rating</Form.Label>
                                   <Form.Control
                                      as='select'
                                      value={rating}
                                      onChange = {e => setRating(e.target.value)}
                                   >
                                       <option value=''>Select...</option>
                                       <option value='1'>1 - Poor</option>
                                       <option value='2'>2 - Fair</option>
                                       <option value='3'>3 - Good</option>
                                       <option value='4'>4 - Very Good</option>
                                       <option value='5'>5 - Excellent</option>
                                   </Form.Control>
                               </Form.Group>

                               <Form.Group
                                 controlId='comment'
                               >
                                <Form.Label>Review</Form.Label>
                                <Form.Control
                                   as='textarea'
                                   row={5}
                                   value={comment}
                                   onChange={e => setComment(e.target.value)}
                                >

                                </Form.Control>

                               </Form.Group>

                               <Button
                                 className='my-4'
                                 variant='primary'
                                 type='submit'
                                 disabled={loadingProductReview}
                               >
                                Submit
                               </Button>
                            </Form>) : (
                                <Message>Please <Link to='/login'>login</Link> to write a review</Message>
                            )
                            }
                            
                        </ListGroup.Item>
                    </ListGroup>
                </Col>
            </Row>
            </div> 
            )
        }
       
    </div>
  )
}

export default ProductScreen